define("appkit/components/pretty-color", 
  ["exports"],
  function(__exports__) {
    "use strict";
    __exports__["default"] = Ember.Component.extend({
        classNames: ['pretty-color'],
        attributeBindings: ['style'],
        style: function() {
            return 'color: ' + this.get('name') + ';';
        }.property('name')
    });
  });